import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
            access_token: null
		};
	}


    handleClick(access_token) {
        connect.send('VKWebAppCallAPIMethod', {
            'method': 'wall.get',
            'params': { 'owner_id': '-86529522', 'v': '5.92', 'access_token': access_token }
        });
    };

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
                case 'VKWebAppAccessTokenReceived': {
                    console.log('VKWebAppAccessTokenReceived', e.detail.data.access_token);
                    this.setState({ access_token: e.detail.data.access_token });
                    break;
                }
                case 'VKWebAppAccessTokenFailed': {
                    console.log('VKWebAppAccessTokenFailed', e.detail.data);
                    break;
                }
                case 'VKWebAppCallAPIMethodResult': {
                    console.log('VKWebAppCallAPIMethodResult', e.detail.data);
                    break;
                }
                case 'VKWebAppCallAPIMethodFailed': {
                    console.log('VKWebAppCallAPIMethodFailed', e.detail.data);
                    break;
                }
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
        connect.send("VKWebAppGetAuthToken", {"app_id": 6926844, "scope": "friends,status,groups,wall"});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} handleClick={() => this.handleClick(this.state.access_token)} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
